.wea-debug-route-menu {
  width: 200px;
  height: 100%;
  position: fixed;
  left: -200px;
  top: 0;
  background: #fff;
  z-index: 999;
  -webkit-transition: left 0.15s ease-in-out;
  -o-transition: left 0.15s ease-in-out;
  transition: left 0.15s ease-in-out;
}
.wea-debug-route-menu-show {
  left: 0px;
  border-right: 1px solid rgba(88, 175, 243, 0.6);
  -webkit-box-shadow: 1px 0px 10px rgba(88, 175, 243, 0.9);
          box-shadow: 1px 0px 10px rgba(88, 175, 243, 0.9);
}
.wea-debug-route-menu-btn {
  width: 18px;
  height: 60px;
  position: absolute;
  top: 50%;
  right: -3px;
  margin-top: -30px;
  z-index: 11;
  cursor: pointer;
  background-size: 100% 100%;
  background: url(/cloudstore/resource/pc/com/images/right-hide.png) no-repeat 0 0;
}
.wea-debug-route-menu-btn-show {
  right: -18px;
  background: url(/cloudstore/resource/pc/com/images/right-show.png) no-repeat -1px 0;
}



