.positionLayer {
  width: 100vw;
  height: 100vh;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
}
.positionLayer > div {
  z-index: 100;
  visibility: hidden;
  will-change: transform, top;
  width: 0;
  height: 0;
  position: absolute;
  left: 50vw;
}
.positionLayer > div .loadError {
  position: absolute;
  top: 50%;
  left: 50%;
  margin-left: -120px;
  margin-top: -80px;
}



