.icon-button{
    font-size: 16px;
    color: #484848;
    cursor: pointer;
}

.wea-drop-menu {
    position: relative;
    display: inline-block;
}

.wea-drop-menu .wea-new-top-drop-btn{
    display: inline-block;
    vertical-align: middle;
    cursor: pointer;
}

.wea-drop-menu .wea-btn-top {
    line-height: 40px;
    width: 40px;
    text-align: center;
    padding-left: 0;
    border: 1px solid transparent;
}

.wea-drop-menu .wea-new-top-drop-menu {
    max-width: 200px;
    position: absolute;
    right: 0;
    border: 1px solid #dadada;
    -webkit-box-shadow: 0 0 2px #dadada;
            box-shadow: 0 0 2px #dadada;
    z-index: 99;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
}
.wea-drop-menu .wea-new-top-drop-menu .wea-new-top-drop-btn{
    display: block;
    position: absolute;
    right: 0px;
    height: 39px;
    top: -39px;
    padding: 0 10px;
    border: 1px solid #dadada;
    border-bottom: 0;
    -webkit-box-shadow: 0px -1px 2px -1px #dadada;
            box-shadow: 0px -1px 2px -1px #dadada;
    background-color: #fff;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
}
.wea-drop-menu .ant-btn[disabled],
.wea-drop-menu .ant-btn[disabled]:hover{
    background-color: #d9d9d9;
    color: #fff;
}

.wea-drop-menu .icon-button.wea-new-top-btn-clicked{
    color:#0077d1;
}


