.special-field-wrapper .special-field-item {
  height: 30px;
  line-height: 30px;
  padding-left: 15px;
  font-size: 12px;
}
.special-field-wrapper .special-field-item.disabled {
  color: #999999;
}
.special-field-wrapper .special-field-item.actived {
  cursor: pointer;
}
.special-field-wrapper .special-field-item.actived:hover {
  background-color: #e9f7fe;
}
.special-field-wrapper .wea-select {
  width: 100%;
  border: none;
}
.special-field-wrapper .wea-input-focus {
  width: 100%;
}
.special-field-wrapper-single {
  display: inline-block;
  width: 180px;
  padding-bottom: 4px;
}
.special-field-wrapper-title {
  display: inline-block;
  width: 180px;
  line-height: 30px;
  height: 30px;
  padding-left: 15px;
  font-weight: 600;
  border-bottom: 1px solid #e4e4e4;
  background: #f9f9f9;
}



