.wea-signature {
  border: 1px solid #eaeaea;
  height: 100%;
  position: relative;
}
.wea-signature-clearBoth {
  clear: both;
  content: "";
}
.wea-signature-tools {
  height: 50px;
  line-height: 30px;
  background: #f7f7f7;
  -webkit-box-shadow: 0 1px 0 0 #eaeaea;
          box-shadow: 0 1px 0 0 #eaeaea;
  padding: 10px 5px;
  -moz-user-select: none;
  -webkit-user-select: none;
  -ms-user-select: none;
  user-select: none;
  position: relative;
  float: none;
  height: auto;
}
.wea-signature-tools-single,
.wea-signature-tools-singleLast {
  float: left;
  padding: 0 12px;
  border-right: 1px solid #e2e2e2;
  min-height: 33px;
}
.wea-signature-tools-single .wea-color-picker,
.wea-signature-tools-singleLast .wea-color-picker {
  width: auto;
}
.wea-signature-tools-singleLast {
  border: 0;
}
.wea-signature-tools-mode {
  color: #aeaeae;
  vertical-align: text-bottom;
  margin-right: 5px;
}
.wea-signature-tools-dot {
  width: 18px;
  height: 18px;
  border: 1px solid transparent;
  display: inline-block;
  vertical-align: middle;
  margin: 0 8px;
  cursor: pointer;
}
.wea-signature-tools-dot-selected {
  border: 1px solid #d2d2d2;
  background: #fff;
}
.wea-signature-tools-dot-xs,
.wea-signature-tools-dot-md,
.wea-signature-tools-dot-lg {
  width: 3px;
  height: 3px;
  border-radius: 50%;
  background: #8f8f8f;
  margin-top: 7px;
  margin-left: 7px;
}
.wea-signature-tools-dot-md {
  width: 6px;
  height: 6px;
  margin-top: 5px;
  margin-left: 5px;
}
.wea-signature-tools-dot-lg {
  width: 8px;
  height: 8px;
  margin-top: 4px;
  margin-left: 4px;
}
.wea-signature-tools-extraBtns {
  display: inline-block;
}
.wea-signature-tools-extraBtns-single {
  color: #333;
  display: inline-block;
  padding-right: 12px;
  vertical-align: middle;
  cursor: pointer;
}
.wea-signature-tools-extraBtns-single:last-child {
  padding-right: 0;
}
.wea-signature-tools-extraBtns-single-icon {
  padding-right: 8px;
  color: #aeaeae;
  vertical-align: text-bottom;
}
.wea-signature-tools-color {
  width: 26px;
  height: 26px;
  display: inline-block;
  vertical-align: middle;
  margin-left: 8px;
  margin-right: 2px;
  cursor: pointer;
}
.wea-signature-tools-normalColors {
  float: right;
  margin-top: 3px;
  line-height: 1;
}
.wea-signature-tools-normalColors-single {
  cursor: pointer;
  display: inline-block;
  width: 12px;
  height: 12px;
  margin-bottom: 1px;
  margin-right: 1px;
  margin-left: 1px;
}
.wea-signature-content,
.wea-signature-contentResult {
  position: relative;
  -moz-user-select: none;
  -webkit-user-select: none;
  -ms-user-select: none;
  user-select: none;
  height: 100%;
}
.wea-signature-content-checkEraser,
.wea-signature-contentResult-checkEraser {
  cursor: url("/cloudstore/resource/pc/com/images/signature-cursor.ico"), auto;
}
.wea-signature-content input,
.wea-signature-contentResult input {
  width: 100%;
  border: 0;
  border-bottom: 1px solid #eaeaea;
  display: block;
  font-size: 12px;
  -moz-user-select: none;
  -webkit-user-select: none;
  -ms-user-select: none;
  user-select: none;
}
.wea-signature-content-canvas,
.wea-signature-contentResult-canvas {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
}
.wea-signature-content-result,
.wea-signature-contentResult-result {
  position: absolute;
  z-index: 10;
  top: 50px;
  width: 100%;
}
.wea-signature-content-result-row,
.wea-signature-contentResult-result-row {
  height: 40px;
  width: 100%;
  color: #fff;
  caret-color: #000;
  border-bottom: 1px solid #eaeaea;
  font-size: 12px;
  -moz-user-select: none;
  -webkit-user-select: none;
  -ms-user-select: none;
  user-select: none;
}
.wea-signature-content-result-row:focus,
.wea-signature-contentResult-result-row:focus {
  outline: none;
}
.wea-signature-content-result img,
.wea-signature-contentResult-result img {
  height: 40px;
  float: left;
}
.wea-signature-content-result img:after,
.wea-signature-contentResult-result img:after {
  clear: both;
  content: "";
}
.wea-signature-content {
  position: absolute;
  width: 100%;
}



