.wea-rich-text {
  min-height: 30px;
  background-color: #fff;
}

.wea-rich-text .cke {
  border: none;
  width: auto;
}

.wea-rich-text .cke .cke_inner {
  border: 1px solid #eaeaea;
}

.wea-rich-text .cke .cke_inner .cke_top {
  border-bottom: 1px solid #eaeaea;
  overflow: inherit;
}

.wea-rich-text .cke .cke_inner .cke_top:after,
.wea-rich-text .cke .cke_inner .cke_contents:after,
.wea-rich-text .cke .cke_inner .cke_bottom:after {
  clear: both;
  content: ".";
  display: block;
  font-size: 0;
  height: 0;
  _zoom: 1;
}

.wea-rich-text .cke .cke_inner .cke_bottom {
  border-top: none;
  background-color: #fff;
}

.wea-rich-text .cke_inner .cke_bottom .cke_path {
  display: none;
}

.wea-rich-text .cke_inner .cke_bottom .cke_resizer_ltr {
  margin-right: 4px;
}

.wea-rich-text .wea-rich-text-toolbar-bottom {
  width: 100%;
  line-height: 30px;
  padding: 4px 8px;
  border: 1px solid #eaeaea;
  border-top: none;
  background-color: #fff;
}

.wea-rich-text .wea-rich-text-toolbar-bottom .wea-rich-text-toolbar-bottom-item {
  display: inline-block;
  padding: 4px 8px;
  cursor: pointer;
}

.wea-rich-text .wea-rich-text-toolbar-bottom .wea-rich-text-toolbar-bottom-item .wea-upload {
  padding: 0;
}

.wea-rich-text .cke_button {
  cursor: pointer !important;
}

.wea-rich-text .cke_toolgroup {
  margin: 0 2px 0 0;
}

.wea-rich-text .cke_toolbar_separator {
  margin-bottom: 4px;
}

.wea-rich-text .cke_combo {
  height: 26px;
  margin-bottom: 0;
}
/**/

.wea-rich-text .cke_button.cke_button__maximize {
  float: right;
}

.wea-rich-text .cke_button_disabled,
.wea-rich-text .wea-rich-text-toolbar-bottom-item-disabled,

/**/

.wea-rich-text .cke_button_disabled .cursor-pointer,
.wea-rich-text .cke_button_disabled i {
  color: #d6d6d6 !important;
  cursor: not-allowed !important;
}

.wea-rich-text .wea-rich-text-not-allowed {
  width: 100%;
  height: 100%;
  position: absolute;
  cursor: not-allowed;
  top: 0;
  left: 0;
  z-index: 2;
}

.wea-rich-text .wea-rich-text-extents {
  display: inline-block;
  height: 33px;
  float: left;
}

.wea-rich-text .wea-rich-text-extents .wea-rich-text-extents-line {
  display: inline-block;
  height: 18px;
  width: 0;
  border-right: 1px solid #bcbcbc;
  margin: 5px 2px 0 0;
  float: left;
}

.wea-rich-text .wea-rich-text-extents-maximize {
  float: right;
}

.wea-rich-text .wea-rich-text-extents .cke_toolgroup a.cke_button:last-child:after,
.wea-rich-text .wea-rich-text-extents .cke_toolgroup a.cke_button.cke_button_disabled:hover:last-child:after,
.wea-rich-text .wea-rich-text-extents-maximize .cke_toolgroup a.cke_button:last-child:after,
.wea-rich-text .wea-rich-text-extents-maximize .cke_toolgroup a.cke_button.cke_button_disabled:hover:last-child:after {
  display: none;
}

.wea-rich-text .wea-rich-text-extents-maximize .cke_toolgroup {
  margin: 0;
  padding: 0;
}

.wea-rich-text .wea-rich-text-extents .cke_button i {
  font-size: 16px;
  cursor: pointer;
}

.wea-rich-text-dialog-image.wea-workflow-hr-radio .ant-tabs-nav-container {
  position: inherit
}

.wea-rich-text .cke_combo_text {
  width: 50px;
}

/**/

.wea-rich-text-maximize {
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  z-index: 99;
}

.wea-rich-text-maximize .cke .cke_inner .cke_bottom {
  display: none;
}

.cke_combopanel_fontsize ol, .cke_combopanel_fontsize ul, .cke_combopanel_fontsize dl {
  line-height: normal;
}



