.wea-loading-global {
  position: fixed;
  z-index: 99999;
  top: 0;
  left: 0;
}
.wea-loading-global .ant-spin-nested-loading,
.wea-loading-global-content,
.wea-loading-global,
.wea-loading-global .ant-spin-blur {
  width: 100%;
  height: 100%;
}
.wea-loading-global-content {
  background-color: #fff;
  opacity: 0.5;
}



