.wea-avatar {
  position: relative;
  display: inline-block;
  vertical-align: top;
  width: 100%;
  height: 100%;
  border-radius: 50%;
  text-align: center;
  color: #fff;
  background-color: #55b1f9;
}
.wea-avatar .wea-avatar-img {
  width: 100%;
  height: 100%;
  border-radius: 50%;
}
.wea-avatar > span {
  position: absolute;
  top: 50%;
  left: 50%;
  width: 100%;
  -webkit-transform: translate(-50%, -50%);
      -ms-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  font-size: 14px;
  border-radius: 50%;
}



