.wea-dialog-footer {
  padding: 15px 18px 14px 10px;
  border-radius: 0 0 6px 6px;
  text-align: right;
  position: fixed;
  bottom: 0;
  width: 100%;
  z-index: 2;
}
.wea-dialog-footer.bTop {
  border-top: 1px solid #e9e9e9;
}
.wea-dialog-footer button {
  border-radius: 3px;
  margin-right: 10px;
}
.wea-dialog-footer button:last-child {
  margin-right: 0;
}



