.wea-radio-group {
  border: 1px solid #ddd;
  border-radius: 3px;
}
.wea-radio-group .wea-form-item-label {
  color: #333;
  padding-top: 6px;
  padding-left: 16px;
  height: 100%;
}
.wea-radio-group .wea-select {
  width: auto;
}
.wea-radio-group .wea-form-item {
  min-height: 42px;
  border-bottom: 1px solid #ddd;
}
.wea-radio-group .wea-form-item:last-child {
  border-bottom: none;
}
.wea-radio-group .wea-form-item-wrapper {
  padding-left: 8px;
}
.wea-radio-group .wea-form-item-wrapper .wea-select {
  margin-top: 8px;
}
.wea-radio-group .wea-form-item-wrapper .ext-com-wrapper {
  max-width: 400px;
  margin-top: 4px;
}
.wea-radio-group .wea-form-item-wrapper .ext-com-wrapper .wea-browser {
  margin-top: 2px;
}



