/**/
.wea-drop-menu .ant-dropdown-link{
	display: inline-block;
    width: 100%;
    height: 37px;
    line-height: 37px;
    color: #494949;
    font-size: 14px;
    border-bottom: 1px solid #e7e7e7;
    padding-left: 10px;
}
.wea-drop-menu .ant-dropdown-link>i,
.wea-drop-menu .my-tree .icon-toolbar-Organization-list{
    margin-right: 10px;
    font-size: 16px;
}
.wea-drop-menu .ant-dropdown-link .anticon{
    margin-right: 5px;
    float: right;
    padding: 15px 10px;
}
.wea-drop-menu .ant-dropdown-link .icon-down{
  position: absolute;
  right: -5px;
}







