.wea-transfer-right {
  position: relative;
  border: 1px solid #dadada;
  border-top: none;
}
.wea-transfer-right .wea-scroll {
  height: 100%;
}
.wea-transfer-right .ant-tree-checkbox-inner {
  border-radius: initial !important;
  top: 5px;
}
.wea-transfer-right .wea-input-focus > span > input {
  border-right: none;
}
.wea-transfer-right .transfer-tree li {
  margin: 0;
  border-bottom: 1px solid #e9e9e9;
}
.wea-transfer-right .ant-tree {
  padding: 0;
}
.wea-transfer-right .ant-tree-switcher {
  display: none !important;
}
.wea-transfer-right .ant-tree-checkbox {
  display: none;
}
.wea-transfer-right .ant-tree-node-content-wrapper {
  width: 100%;
  padding: 6px 0 6px 20px !important;
  overflow-x: hidden;
  color: #484848;
}
.wea-transfer-right .tree-title {
  width: 100%;
}
.wea-transfer-right .resource-content {
  width: 100%;
  display: inline-block;
  padding-right: 45px;
  vertical-align: top;
}
.wea-transfer-right .gray {
  color: #aaa;
}
.wea-transfer-right .overflow {
  -o-text-overflow: ellipsis;
     text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
}
.wea-transfer-right .wea-tree-hr-portrait {
  width: 30px;
  height: 30px;
  border-radius: 15px;
  margin-right: 15px;
  vertical-align: initial;
}
.wea-transfer-right .lastname {
  display: inline-block;
  min-width: 60px;
}
.wea-transfer-right .tree-title i {
  margin-right: 15px;
  font-size: 18px;
  width: 30px;
  display: inline-block;
  text-align: center;
  color: #d1d1d1;
}
.wea-transfer-right .ant-tree li a.ant-tree-node-selected {
  background-color: #eaf8fe;
}
.wea-transfer-right .ant-tree li a {
  position: relative;
  vertical-align: middle;
}
.ant-tree li .icon-coms-Selected {
  display: none;
}
.wea-transfer .wea-icon-selected-bg,
.wea-transfer-right .wea-icon-selected-bg {
  display: none;
  position: absolute;
  top: 0px;
  right: 0px;
  height: 0;
  width: 0;
  border-top: 21px solid #0b93da;
  border-left: 22px solid transparent;
}
.wea-transfer .icon-coms-Selected,
.wea-transfer-right .icon-coms-Selected {
  display: none;
  position: absolute;
  font-size: 12px;
  color: #fff;
  top: 0;
  right: 1px;
}
.wea-transfer-right .ant-tree li a.ant-tree-node-selected .wea-icon-selected-bg {
  display: block;
}
.wea-transfer-right .ant-tree li a.ant-tree-node-selected .icon-coms-Selected {
  display: block;
}



