.wea-color-picker {
    display: inline-block;
    position: relative;
    width: 100px;
}

.wea-color-picker-popover .ant-popover-arrow {
    display: none;
}

.wea-color-picker-popover .ant-popover-inner-content {
    padding: 0;
}

.wea-color-picker-panel .rc-color-picker-panel-inner {
    border: none;
    -webkit-box-shadow: none;
            box-shadow: none;
    border-radius: 0;
}
.wea-color-picker-btn {
    position: absolute;
    z-index: 1;
    top: 7px;
    right: 7px;
    width: 15px;
    height: 15px;
    border: 1px solid #eee;
    cursor: pointer;
}

.wea-color-picker-btn.color-picker-disabled {
    cursor: not-allowed;
}


