.wea-search-input-search {
  display: inline-block;
  width: 100%;
  height: 36px;
  line-height: 34px;
  position: relative;
}
.wea-search-input-search .input {
  width: 100%;
  height: 36px;
  border: 1px solid #2DB7F5;
  outline: none;
  padding-left: 10px;
}
.wea-search-input-search .input-text {
  position: absolute;
  top: 0;
  right: 0;
  display: inline-block;
  padding: 0 20px;
  height: 36px;
  line-height: 36px;
  text-align: center;
  background: #2DB7F5;
  color: #ffffff;
  float: right;
  cursor: pointer;
}
.wea-search-input-search .input-text:hover {
  background: #0695d9;
}
.wea-search-input-search .input-left {
  position: absolute;
  top: 0;
  left: 0;
  height: 36px;
  display: inline-block;
  padding: 0 10px;
  cursor: pointer;
}
.wea-search-input-search .input-left > em {
  font-size: 16px;
  color: #2DB7F5;
  vertical-align: middle;
}
.wea-search-input-search input::-webkit-input-placeholder {
  /*WebKit browsers*/
  color: #999;
}
.wea-search-input-search input:-ms-input-placeholder {
  /*Internet Explorer*/
  color: #999;
}
.wea-search-input-hotkeys-popover {
  padding-top: 0 !important;
}
.wea-search-input-hotkeys-popover .ant-popover-arrow {
  display: none;
}
.wea-search-input-hotkeys-popover .ant-popover-inner {
  border: none;
  border-radius: 0;
  -webkit-box-shadow: none;
          box-shadow: none;
}
.wea-search-input-hotkeys-popover .ant-popover-inner-content {
  padding: 0;
}
.wea-search-input-hotkeys-popover .input-hotkeys {
  border: 1px solid #e8e8e8;
  border-radius: 2px;
  border-top: none;
  width: 100%;
  z-index: 100;
  background: #fff;
  margin-top: -4px;
}
.wea-search-input-hotkeys-popover .input-hotkeys > li {
  cursor: pointer;
}
.wea-search-input-hotkeys-popover .input-hotkeys .hotkeys-background {
  background: #E9F7FF;
}
.wea-search-input-hotkeys-popover .input-hotkeys .hotkeys-hide-background {
  background: #ffffff;
}
.wea-search-input-hotkeys-popover .input-hotkeys > li {
  padding-left: 10px;
  line-height: 24px;
  height: 24px;
  overflow: hidden;
  max-width: 100%;
  -o-text-overflow: ellipsis;
     text-overflow: ellipsis;
  white-space: nowrap;
}
.wea-search-input-hotkeys-popover .input-hotkeys > li:hover {
  background: #56C5F7;
}



