.wea-right-menu {
  background-color: #fff;
  border: 1px solid #eaeaea;
  -webkit-box-shadow: 0 0 5px #ddd;
          box-shadow: 0 0 5px #ddd;
  min-width: 120px;
  z-index: 99999;
}
.wea-right-menu-wrap {
  height: 100%;
  position: relative;
}
.wea-right-menu-icon-background {
  width: 36px;
  height: 100%;
  background-color: #f2f5f7;
  border: 1px solid #eaeaea;
  border-right: 0;
  position: absolute;
  top: -1px;
  left: -1px;
  z-index: 0;
  -webkit-box-sizing: content-box;
          box-sizing: content-box;
}
.wea-right-menu .ant-menu {
  margin: 10px 0;
  border: 0;
  text-align: left;
}
.wea-right-menu .ant-menu .ant-menu-item {
  height: 30px;
  line-height: 30px;
  padding: 0 8px 0 0;
  overflow: hidden;
  white-space: nowrap;
  -o-text-overflow: ellipsis;
     text-overflow: ellipsis;
}
.wea-right-menu .ant-menu .ant-menu-item .wea-right-menu-icon {
  display: inline-block;
  width: 36px;
  margin-right: 6px;
  text-align: center;
}
.wea-right-menu .ant-menu .ant-menu-item-selected {
  border: 0;
  color: #000;
}
.wea-right-menu .ant-menu .ant-menu-item-active {
  background-color: #57c5f7;
  color: #fff;
}
.wea-right-menu iframe.wea-right-menu-iframe {
  width: 100%;
  height: 100%;
  border: 0;
  background-color: transparent;
  position: absolute;
  top: 0;
  left: 0;
  z-index: -1;
}
.wea-right-menu-url {
  padding: 20px;
  position: relative;
}
.wea-right-menu-url .wea-input-normal .ant-input {
  padding-right: 30px;
}
.wea-right-menu-url .icon-coms-copy {
  position: absolute;
  right: 30px;
  top: 26px;
  font-size: 16px;
  cursor: pointer;
  color: #999;
}
.wea-right-menu-url .icon-coms-copy:hover {
  color: #57c5f5;
}



