@keyframes fadeIn {
  0% {
    opacity: 0;
  }
  50% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
@-webkit-keyframes fadeIn {
  0% {
    opacity: 0;
  }
  50% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
.wea-search-panel {
  position: relative;
}
.wea-search-panel .wea-search-input-wrapper {
  display: inline-block;
  margin-right: 15px;
  position: relative;
}
.wea-search-panel .wea-search-panel-show {
  animation: fadeIn 0.4s linear;
  -webkit-animation: fadeIn 0.4s linear;
}
.wea-search-panel .btn-footer {
  text-align: center;
  border-top: 1px solid #e2e2e2;
  padding: 15px 0;
}
.wea-search-panel .btn-footer button {
  border-radius: 3px;
  margin-right: 10px;
}
.wea-search-panel .btn-footer button:last-child {
  margin-right: 0;
}
.wea-search-panel .wea-search-input-wrapper {
  width: 449px;
  white-space: nowrap;
}
.wea-search-panel .wea-search-panel-text {
  position: relative;
  display: inline-block;
  padding-bottom: 2px;
}
.wea-search-panel .wea-search-panel-adSearch {
  position: relative;
  bottom: -10px;
}
.wea-search-panel .wea-border-triangle {
  position: relative;
  top: 26px;
  right: -34px;
  z-index: 1001;
  opacity: 1;
  display: inline-block;
  width: 0;
  border: 6px solid transparent;
  border-bottom: 6px solid #D9D9D9;
}
.wea-search-panel .wea-border-triangle::before {
  position: absolute;
  content: '';
  top: -5px;
  left: -7px;
  width: 0;
  height: 0;
  border: 7px solid transparent;
  border-bottom: 7px solid #fff;
}
.wea-search-panel .wea-search-panel-search-wrapper {
  opacity: 1;
  width: 563px;
  position: absolute;
  top: 48px;
  background: #fff;
  border: 1px solid #D9D9D9;
  border-radius: 6px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  -webkit-box-shadow: 0 1px 6px rgba(99, 99, 99, 0.2);
          box-shadow: 0 1px 6px rgba(99, 99, 99, 0.2);
  z-index: 1000;
}
@media (min-width: 768px) {
  .wea-search-panel .wea-search-input-wrapper {
    width: 449px;
  }
  .wea-search-panel .wea-search-panel-search-wrapper {
    width: 563px;
  }
}
@media (min-width: 992px) {
  .wea-search-panel .wea-search-input-wrapper {
    width: 480px;
  }
  .wea-search-panel .wea-search-panel-search-wrapper {
    width: 600px;
  }
}
@media (min-width: 1200px) {
  .wea-search-panel .wea-search-input-wrapper {
    width: 520px;
  }
  .wea-search-panel .wea-search-panel-search-wrapper {
    width: 677px;
  }
}
.wea-search-panel .wea-mask-dark {
  background: transparent;
  opacity: 0;
}



