.wea-search-group {
  padding-left: 25px;
  padding-right: 25px;
}
.wea-search-group .wea-content .wea-form-cell-wrpper {
  width: 100%;
  display: inline-block;
}
.wea-search-group .wea-content.center {
  text-align: center;
}
.wea-search-group .wea-content.center .wea-form-item {
  padding-left: 0;
}
.wea-search-group .wea-content.center .wea-form-cell-wrapper {
  width: 300px;
  display: inline-block;
}
.wea-search-group .wea-content.center .wea-form-cell-wrapper.small {
  width: 300px !important;
}
.wea-search-group .wea-content.center .wea-form-cell-wrapper.middle {
  width: 400px !important;
}
.wea-search-group .wea-content.center .wea-form-cell-wrapper.large {
  width: 500px !important;
}
.wea-search-group .wea-content.center .wea-form-item {
  text-align: left;
}
@media (min-width: 768px) {
  .wea-search-group .wea-content.center .wea-form-cell-wrapper {
    width: 350px;
  }
}
@media (min-width: 992px) {
  .wea-search-group .wea-content.center .wea-form-cell-wrapper {
    width: 510px;
  }
}
@media (min-width: 1200px) {
  .wea-search-group .wea-content.center .wea-form-cell-wrapper {
    width: 640px;
  }
}
@media (min-width: 768px) {
  .wea-search-group .wea-content.center.multiCol .wea-form-cell-wrapper {
    width: 345px;
  }
}
@media (min-width: 992px) {
  .wea-search-group .wea-content.center.multiCol .wea-form-cell-wrapper {
    width: 510px;
  }
}
@media (min-width: 1200px) {
  .wea-search-group .wea-content.center.multiCol .wea-form-cell-wrapper {
    width: 641px;
  }
}
.wea-search-group .wea-title {
  color: #484848;
  padding-left: 10px;
  border-bottom: solid 1px #ebebeb;
  line-height: 45px;
}
.wea-search-group .wea-title i {
  cursor: pointer;
  color: #b2b2b2;
}
.wea-search-group .wea-content {
  padding: 8px 0;
  -webkit-transition: height 0.2s cubic-bezier(0.215, 0.61, 0.355, 1);
  -o-transition: height 0.2s cubic-bezier(0.215, 0.61, 0.355, 1);
  transition: height 0.2s cubic-bezier(0.215, 0.61, 0.355, 1);
}
.wea-search-group .wea-content.pt15 {
  padding-top: 8px;
}
.wea-search-group .center .wea-form-cell {
  padding-left: 0;
  padding-right: 0;
}
.wea-search-group .wea-form-cell {
  padding: 0 10px 0 30px;
  min-height: 0;
}
.wea-search-group .wea-form-item {
  min-height: 30px;
  padding-top: 8px;
  padding-bottom: 8px;
}
.wea-search-group .wea-form-item .wea-upload {
  padding-top: 0;
}
.wea-search-group .wea-form-item.underline {
  padding-top: 0;
  min-height: 38px;
}
/* group*/
.wea-form-cell .ant-input-wrapper input,
.wea-form-cell .ant-input-wrapper .ant-input-group-addon,
.wea-form-cell .ant-select-selection,
.wea-form-cell .ant-calendar-range-picker {
  border-radius: 0;
  background-color: #fff;
}
.wea-search-group .wea-form-cell .ant-search-custom .ant-input-wrapper input {
  border-right: 0;
}
/**/
.wea-search-group .wea-form-cell input.ant-input,
.wea-search-group .wea-form-cell .ant-select-selection {
  -webkit-box-shadow: 0 0 0 transparent !important;
          box-shadow: 0 0 0 transparent !important;
  border-color: #d9d9d9;
}
.wea-search-group .ant-calendar-range-picker.ant-input {
  position: relative;
  top: 1px;
}
.wea-search-group .wea-form-cell .ant-select-selection:hover,
.wea-search-group .wea-form-cell .ant-select-selection:focus,
.wea-search-group .wea-form-cell input.ant-input:hover,
.wea-search-group .wea-form-cell .wea-associative-search:hover,
.wea-search-group .wea-form-cell input.ant-input:focus {
