.wea-app-center {
  width: 100%;
  height: 100%;
}
.wea-app-center .clearBoth {
  clear: both;
  content: '';
}
.wea-app-center .wea-app-center-con {
  margin: 20px 40px;
  position: relative;
}
.wea-app-center .wea-app-center-con .slider,
.wea-app-center .wea-app-center-con .slider-frame,
.wea-app-center .wea-app-center-con .slider-list {
  height: 100%!important;
}
.wea-app-center .wea-app-center-con .slider .slider-slide,
.wea-app-center .wea-app-center-con .slider-frame .slider-slide,
.wea-app-center .wea-app-center-con .slider-list .slider-slide {
  overflow-x: auto;
  overflow-y: hidden;
}
.wea-app-center .wea-app-center-con .slider-frame {
  width: 100%;
}
.wea-app-center .wea-app-center-con .slider-control-bottomcenter ul {
  top: 5px!important;
}
.wea-app-center .wea-app-center-con .slider-control-bottomcenter ul li button {
  color: #00A9FF !important;
}
.wea-app-center .wea-app-center-con .single-page {
  padding: 0 20px;
}
.wea-app-center .wea-app-center-con .single-page .single-con {
  position: relative;
  float: left;
  height: 104px;
  border: 1px solid transparent;
  line-height: 104px;
  padding: 5px;
}
.wea-app-center .wea-app-center-con .single-page .single-con .single-app {
  cursor: pointer;
  padding-top: 10px;
  text-align: center;
  -webkit-transition: all 0.2s linear;
  -o-transition: all 0.2s linear;
  transition: all 0.2s linear;
}
.wea-app-center .wea-app-center-con .single-page .single-con .single-app .single-icon {
  position: absolute;
  height: 56px;
  line-height: 56px;
  width: 56px;
  margin-top: 24px;
  border-radius: 50%;
  text-align: center;
}
.wea-app-center .wea-app-center-con .single-page .single-con .single-app .single-name {
  padding-left: 65px;
  text-align: left;
  font-weight: 600;
}
.wea-app-center .wea-app-center-con .single-page .single-con .single-desc {
  display: none;
  padding: 5px 10px;
  cursor: pointer;
}
.wea-app-center .wea-app-center-con .single-page .single-con:after {
  clear: both;
  content: '';
}
.wea-app-center .wea-app-center-con .single-page .single-con:hover {
  border: 1px solid #E1E6EC;
  -webkit-box-shadow: 0 2px 8px 0 rgba(0, 0, 0, 0.2);
          box-shadow: 0 2px 8px 0 rgba(0, 0, 0, 0.2);
  border-radius: 4px;
}
.wea-app-center .wea-app-center-con .single-page .single-con:hover .single-app .single-icon {
  -webkit-transform: scale(0.7) translateY(-45px);
          transform: scale(0.7) translateY(-45px);
  -ms-transform: scale(0.7) translateY(-45px);
  -webkit-transition: all 0.2s linear;
  -o-transition: all 0.2s linear;
  transition: all 0.2s linear;
}
.wea-app-center .wea-app-center-con .single-page .single-con:hover .single-app .single-name {
  -webkit-transform: translateY(-30px);
          transform: translateY(-30px);
  -ms-transform: translateY(-30px);
  -webkit-transition: all 0.2s linear;
  -o-transition: all 0.2s linear;
  transition: all 0.2s linear;
}
.wea-app-center .wea-app-center-con .single-page .single-con:hover .single-desc {
  display: inline-block;
  -webkit-transform: translateY(-100px);
          transform: translateY(-100px);
  -ms-transform: translateY(-100px);
  text-align: left;
  width: 100%;
  overflow: hidden;
  text-overflow: ellipsis;
  -o-text-overflow: ellipsis;
  -webkit-text-overflow: ellipsis;
  -moz-text-overflow: ellipsis;
  white-space: nowrap;
  /**/
}



