.wea-dropdown {
  background: #E8E8E8;
  border: 1px solid #D6D6D6;
  padding: 5px 10px;
}
.wea-dropdown .wea-dropdown-triangle {
  border-right: 5px solid transparent;
  border-left: 5px solid transparent;
  width: 0;
  height: 0;
  display: inline-block;
  padding: 0;
  margin-left: 7px;
}
.wea-dropdown .wea-dropdown-triangle.up {
  border-bottom: 7px solid #b2b2b2;
  margin-bottom: 2px;
}
.wea-dropdown .wea-dropdown-triangle.down {
  border-top: 7px solid #b2b2b2;
}



