.atwho-view {
  position: absolute;
  top: 0;
  left: 0;
  display: none;
  margin-top: 18px;
  background: white;
  color: black;
  border: 1px solid #ddd;
  border-radius: 3px;
  -webkit-box-shadow: 0 0 5px rgba(0, 0, 0, 0.1);
          box-shadow: 0 0 5px rgba(0, 0, 0, 0.1);
  width: 150px;
  max-height: 135px;
  overflow: auto;
  z-index: 11110 !important;
}

.atwho-view .cur {
  background: #3366ff;
  color: white;
}
.atwho-view .cur small {
  color: white;
}
.atwho-view strong {
  color: #3366ff;
}
.atwho-view .cur strong {
  color: white;
  font: bold;
}
.atwho-view ul {
  /* width: 100px; */
  list-style: none;
  padding: 0;
  margin: auto;
}
.atwho-view ul li {
  display: block;
  padding: 5px 10px;
  border-bottom: 1px solid #ddd;
  cursor: pointer;
  /* border-top: 1px solid #C8C8C8; */
}
.atwho-view small {
  font-size: smaller;
  color: #777;
  font-weight: normal;
}



