.wea-tree-search-layout,
.wea-tree-search-layout .wea-left,
.wea-tree-search-layout .wea-right {
	height: 100%;
	overflow: hidden;
}
.wea-tree-search-layout .wea-left {
    border-right: 1px solid #eaeaea;
}

.wea-tree-search-layout .wea-right {
    position: relative;
}

.wea-tree-search-layout {
    background-color: #ffffff;
}
.wea-tree-search-layout .wea-tree-search-layout-show-left{
	width: 18px;
	height: 60px;
	position: absolute;
	top: 50%;
	left: 0;
	margin-top: -30px;
	z-index: 11;
	cursor: pointer;
	background-size: 100% 100%;
}



