.backgroundLayer {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  cursor: -webkit-zoom-out;
  cursor: zoom-out;
  -webkit-transition: all 400ms;
  -o-transition: all 400ms;
  transition: all 400ms;
  background-color: rgba(0, 0, 0, 0);
  opacity: 0.5;
}



