.wea-report-group {
  font-size: 12px;
}
.wea-report-group .wea-report-group-header {
  line-height: 40px;
  background-color: #fbfcff;
  border-bottom: 2px solid #e0edf3;
}
.wea-report-group .wea-report-group-item {
  min-height: 40px;
  border-bottom: 1px solid #ddd;
}
.wea-report-group .wea-select {
  width: 100%;
}
.wea-report-group .wea-checkbox {
  margin-right: 5px;
}
.wea-report-group .pl15 {
  padding-left: 15px;
}
.wea-report-group .ant-input,
.wea-report-group .ant-select-selection {
  border-radius: 0px;
}



