.wea-browser-single .wea-workflow-tree-radio-drop-btn {
  margin-right: 15px;
}

/*s*/
.wea-browser-drop-menu {
  width: 100%;
  margin-top: -5px;
  border-radius: 0;
  border-color: #e9e9e9;
}
.wea-browser-drop-menu > li {
  padding-left: 35px;
}
.wea-browser-modal.wea-browser-single .wea-dropdown-link {
  border-top: 1px solid #ccc;
}

.wea-browser-single .my-tree .icon-toolbar-Organization-list {
  margin-right: 10px;
  font-size: 16px;
}
/*e*/

.wea-browser-single .ant-tree-checkbox-inner {
  border-radius: 50%;
}
.wea-browser-single .ant-menu-submenu {
  border-top: 1px solid #ccc;
  border-bottom: 1px solid #ccc;
}
.wea-browser-single .ant-menu-submenu .icon-portal-workflow {
  font-size: 22px;
  vertical-align: middle;
}

.wea-browser-single .my-tree {
  padding-left: 25px;
}

.wea-browser-single .ant-menu-sub {
  position: absolute;
  width: 100%;
  z-index: 2;
}
.wea-browser-single .icon-process-scene {
  margin-right: 5px;
  font-size: 16px;
}

.subordinates-icon {
  position: absolute;
  right: 30px;
  top: 15px;
}

.subordinates-icon.hasDropMenu {
  top: 45px;
}



