.wea-report-card {
  background: #FFFFFF;
  border: 1px solid #E2E2E2;
  border-radius: 3px;
}
.wea-report-card .wea-report-card-header {
  background: #F9F9F9;
  height: 40px;
  line-height: 40px;
  padding: 0 16px;
  font-size: 12px;
  border-top-left-radius: 3px;
  border-top-right-radius: 3px;
}
.wea-report-card .wea-report-card-header .wea-report-card-title {
  font-weight: bolder;
}
.wea-report-card .wea-report-card-header .wea-report-card-ops {
  float: right;
  color: #868686;
}
.wea-report-card .wea-report-card-header .wea-report-card-ops .cursor-pointer:hover,
.wea-report-card .wea-report-card-header .wea-report-card-ops .cursor-pointer:focus {
  cursor: pointer;
  color: #00A9FF;
}
.wea-report-card .wea-report-card-header .wea-report-card-ops .more:hover,
.wea-report-card .wea-report-card-header .wea-report-card-ops .more:focus {
  text-decoration: underline;
}
.wea-report-card .wea-report-card-body {
  padding: 16px;
}



