.wea-steps {
  display: -ms-flexbox;
  /* : IE 10 */
}
.wea-steps .ant-steps-item {
  -ms-flex: 1;
  /* IE 10 */
}
.wea-steps .ant-steps-item:last-child {
  -ms-flex: none;
}
.wea-steps .ant-steps-step {
  position: relative;
}
.wea-steps .ant-steps-description {
  position: absolute;
  width: 200px;
  top: 30px;
  left: -57px;
}
.wea-steps .ant-steps-item-tail {
  padding: 0 32px;
  top: 20px;
  margin-left: 50px;
}
.wea-steps .ant-steps-item-icon {
  width: 40px;
  height: 40px;
  line-height: 40px;
  border-radius: 50%;
  margin-left: 30px !important;
  border: 1px solid #DBDBDB;
}
.wea-steps .ant-steps-item-wait .ant-steps-item-icon {
  border-color: #DBDBDB;
}
.wea-steps .ant-steps-item-process .ant-steps-item-icon {
  background: #4D9BFB;
  border-color: #4D9BFB;
}
.wea-steps .ant-steps-item-finish .ant-steps-item-icon {
  border-color: #4D9BFB;
}
.wea-steps .ant-steps-item-finish .ant-steps-item-icon > .ant-steps-icon {
  color: #4D9BFB;
}



