.wea-browser-table {
	position: relative;
}

.wea-browser-table .check-button {
	border-radius: 3px;
	max-height: 35px; /*fix font-size16 */
}

.wea-browser-table .check-button-wrapper {
	display: inline-block;
	position: absolute;
	left: 10px;
	top: -35px;
}

.wea-browser-table .icon-check-wrapper {
	position: absolute;
	right: 1px;
	top: 1px;
	display: inline-block;
	width: 14px;
	height: 14px;
	font-size: 12px !important; /*fix*/
	border-radius: 7px;
	background-color: #fff;
	color: #2db7f5;
}

.wea-browser-table .icon-check {
	font-weight: bolder;
	font-size: 12px !important; /*fix*/
	padding-left: 2px;
}

/**/
.wea-browser-table .ant-table-placeholder {
	position: absolute;
	top: 55px;
	left: 50%;
	margin-left: -40px;
	border-bottom: none;
}


