.wea-thumbnails-doc {
  position: relative;
}
.wea-thumbnails-doc .ant-card {
  border-radius: 3px;
  height: 119px;
  margin: 20px 0 0 20px;
}
.wea-thumbnails-doc .ant-card:hover {
  border-color: #2db7f5;
  -webkit-box-shadow: 0 0 4px #2db7f5;
          box-shadow: 0 0 4px #2db7f5;
}
.wea-thumbnails-doc .ant-card-body {
  height: 119px;
  padding: 15px 15px 15px 84px;
}
.wea-thumbnails-doc .ant-card-body div {
  display: inline-block;
}
.wea-thumbnails-doc-triangle {
  position: absolute;
  top: -1px;
  left: -1px;
  height: 0;
  width: 0;
  border-top: 10px solid #ed726c;
  border-right: 10px solid transparent;
}
.wea-thumbnails-doc-extra {
  position: absolute;
  right: -10px;
  top: -4px;
}
.wea-thumbnails-doc-image {
  position: absolute;
  left: 15px;
  top: 15px;
  width: 54px;
  height: 54px;
  line-height: 54px;
}
.wea-thumbnails-doc-image i {
  display: inline-block;
  width: 100%;
  height: 100%;
  font-size: 54px;
}
.wea-thumbnails-doc-content {
  width: 100%;
  height: 100px;
  overflow: hidden;
  padding-right: 20px;
  position: relative;
  top: -4px;
  line-height: 20px;
}
.wea-thumbnails-doc-content a {
  cursor: pointer;
}
.wea-thumbnails-doc-content p {
  overflow: hidden;
  white-space: nowrap;
  -o-text-overflow: ellipsis;
     text-overflow: ellipsis;
  color: #484a4d;
}
.wea-thumbnails-doc-content p a {
  color: #484a4d;
}
.wea-thumbnails-doc-content p span.wea-thumbnails-doc-content-subtitle {
  display: inline-block;
  color: #9a9a9a;
  width: 75px;
}
.wea-thumbnails-doc-content-foot {
  position: absolute;
  right: 15px;
  bottom: 12px;
  line-height: 16px;
}
.wea-thumbnails-doc-content-foot p {
  color: #7f8894;
}
.wea-thumbnails-doc-content-foot p > span {
  padding-right: 10px;
}
.wea-thumbnails-doc-content-foot p > span i {
  font-size: 16px;
  color: #3e3a39;
}



