.wea-share-button-list {
  height: 50px;
  padding: 7px 15px;
  border-bottom: 1px solid #ddd;
  cursor: pointer;
}
.wea-share-button-list:hover {
  background: #e7f9ff;
}
.wea-share-button-list-left {
  display: inline-block;
  width: 30px;
  height: 30px;
  line-height: 30px;
}
.wea-share-button-list-right {
  display: inline-block;
  padding-left: 20px;
  width: calc(100% - 40px);
  vertical-align: top;
}
.wea-share-button-list-right-top > span {
  color: #999;
  padding-left: 24px;
}
.wea-share-button-list-right-bottom {
  color: #999;
}



