.wea-upload {
  padding-top: 5px;
}

.wea-upload-container {
  position: relative;
}

.wea-upload-container span {
  display: inline-block;
}

.icon-coms-AddTo {
  display: inline-block;
}

.wea-upload .moxie-shim-html5 input {
  font-size: 12px !important;
}

.wea-upload .wea-upload-icon {
  cursor: pointer;
}

.wea-upload .icon-coms-AddTo {
  color: #a8a8a8;
  font-size: 24px;
}

.wea-upload .icon-coms-AddTo:hover {
  color: #289cff;
}

.wea-upload .wea-required-e9 img {
  margin-top: -7px;
}

/**/

.wea-upload .wea-upload-list:after {
  clear: both;
  content: ".";
  display: block;
  font-size: 0;
  height: 0;
  visibility: hidden;
  _zoom: 1;
}

.wea-upload .wea-upload-imgs-item {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  padding: 5px 0;
  max-width: 800px;
}

.wea-upload .wea-upload-imgs-item-v {
  float: left;
  text-align: center;
  padding: 5px 0;
}

.wea-upload .wea-upload-imgs-item-v-ops {
  min-height: 24px;
  font-size: 16px;
}

.wea-upload .wea-upload-imgs-item-v .wea-upload-imgs-item-v-ops > i {
  display: none;
}

.wea-upload .wea-upload-imgs-item-v:hover .wea-upload-imgs-item-v-ops > i {
  display: inline-block;
}

.wea-upload .wea-upload-imgs-item-h {
  position: relative;
  padding-right: 60px;
}

.wea-upload .wea-upload-imgs-item-h > span {
  display: none;
}

.wea-upload .wea-upload-imgs-item:hover {
  background-color: #f4f9ff;
}

.wea-upload .wea-upload-imgs-item-h:hover > span {
  display: block;
}

/**/

.wea-upload .ant-btn {
  border-radius: 2px;
}

.wea-upload .wea-upload-list-item {
  /*margin-top: 8px;*/
  overflow: hidden;
}

.wea-upload .wea-upload-list-item-file {
  height: 30px;
  line-height: 30px;
  overflow: hidden;
  position: relative;
  white-space: nowrap;
  -o-text-overflow: ellipsis;
     text-overflow: ellipsis;
  max-width: 800px;
}

.wea-upload .wea-upload-list-item-file a,
.wea-upload .wea-upload-list-item-file a:visited {
  color: #000;
}

.wea-upload .wea-upload-list-item-file a:hover {
  color: #289cff !important;
  text-decoration: underline !important;
}

.wea-upload .wea-upload-list-item-file-t {
  padding: 0 70px 0 0;
  min-width: 180px;
}

/*.wea-upload .wea-upload-list-item-file-t:hover {
  padding: 0 120px 0 0;
}*/

.wea-upload .wea-upload-list-item-file-t-nosize {
  padding: 0 10px 0 0;
  min-width: 125px;
}

.wea-upload .wea-upload-list-item-file-t-nosize:hover {
  padding: 0 65px 0 0;
}

.wea-upload .wea-upload-list-item-file-b {
  padding: 0 35px 0 0;
  min-width: 95px;
}

.wea-upload .wea-upload-list-item-file img {
  vertical-align: middle;
  margin-right: 5px;
}

.wea-upload .wea-upload-list-item-file .wea-upload-list-item-ops {
  height: 30px;
  line-height: 30px;
  font-size: 16px;
  color: #666;
  display: none;
  position: absolute;
  right: 0;
  top: 0;
}

.wea-upload .wea-upload-list-item-file .wea-upload-list-item-filesize {
  width: 55px;
  position: absolute;
  right: 10px;
  top: 0;
}

/*.wea-upload .wea-upload-list-item-file:hover .wea-upload-list-item-filesize {
  right: 64px;
}*/

.wea-upload .wea-upload-list-item-file .wea-upload-list-item-del {
  display: none;
  background-color: #fff;
  padding-left: 10px;
  position: absolute;
  right: 9px;
  top: 0;
  cursor: pointer;
  font-size: 10px;
  line-height: 30px;
}

.wea-upload .wea-upload-list-item-file:hover {
  background-color: #eaf8fe;
}

.wea-upload .wea-upload-list-item-file:hover .wea-upload-list-item-ops,
.wea-upload .wea-upload-list-item-file:hover .wea-upload-list-item-del {
  display: block;
  background-color: #eaf8fe;
}

/*  */
.wea-upload .wea-upload-list-item-file .icon-coms-edit {
  display: none;
  padding: 5px;
  margin-right: -22px;
  color: #666;
}

.wea-upload .wea-upload-list-item-file:hover .icon-coms-edit {
  display: inline-block;
}

.wea-upload .wea-upload-list-item-file-noedit {
  width: 100%;
}

.wea-upload .wea-upload-list-item-file-noedit > div {
  overflow: hidden;
  white-space: nowrap;
  -o-text-overflow: ellipsis;
     text-overflow: ellipsis;
  width: 100%;
}

.wea-upload .wea-upload-list-item-file-editable {
  width: 100%;
  overflow: visible;
  padding-right: 22px;
  white-space: nowrap;
}

.wea-upload .wea-upload-list-item-file-editable > div {
  display: inline-block;
  overflow: hidden;
  white-space: nowrap;
  -o-text-overflow: ellipsis;
     text-overflow: ellipsis;
  vertical-align: middle;
  max-width: 100%;
}

.wea-upload .wea-upload-list-item-file .wea-input-normal {
  padding-right: 45px;
}

/*  */
.wea-upload .wea-upload-list-item-file .icon-coms-move {
  padding: 5px;
  vertical-align: middle;
  color: #666;
}

.wea-upload .wea-upload-list-item-file .icon-coms-move:hover {
  color: #289cff;
}

.wea-upload .wea-upload-imgs-item {
  position: relative;
}

.wea-upload .wea-upload-imgs-item .icon-coms-move {
  background-color: #000;
  color: #fff;
  opacity: 0;
  position: absolute;
  top: -3px;
  left: -3px;
  padding: 3px;
  border-radius: 50%;
  font-size: 14px;
  color: #666;
}

.wea-upload .wea-upload-imgs-item:hover .icon-coms-move {
  opacity: 0.6;
}



