.wea-browser-thumbnails .wea-input-focus > span > input {
  border-left: 1px solid #ccc !important;
  height: auto !important;
}
.wea-thumbnails-table .ant-row > div {
  background: transparent;
  border: 0;
}
.wea-thumbnails .wea-search-advance .wea-input-focus {
  display: inline-block;
  width: inherit;
}
.wea-thumbnails .wea-search-advance {
  border-bottom: 1px solid #e9e9e9 !important;
}
.wea-thumbnails .wea-search-advance .wea-search-tab .wea-input-focus .wea-input-focus-btn {
  padding-bottom: 9px;
}
.wea-thumbnail-table-body {
  padding-top: 10px;
  padding-left: 20px;
}
.wea-thumbnail-filter {
  float: left;
  padding-left: 12px;
  height: 46px;
  line-height: 46px;
}
.wea-thumbnails-box {
  width: 140px;
  padding-bottom: 10px;
  -o-text-overflow: ellipsis;
     text-overflow: ellipsis;
  white-space: nowrap;
  word-break: keep-all;
  overflow: hidden;
  position: relative;
  color: #242424;
  vertical-align: middle;
  background-color: #fff;
  text-align: left;
}
.wea-thumbnails-img-wrap {
  width: 140px;
  height: 152px;
  text-align: center;
  line-height: 152px;
  cursor: pointer;
  border: 1px solid #e3e3e3;
  border-collapse: collapse;
}
.wea-thumbnails-img-wrap img {
  max-width: 140px;
  max-height: 145px;
  vertical-align: middle;
}
.wea-thumbnails-op {
  top: 123px;
  left: 13px;
  position: absolute;
  display: block;
}
.wea-thumbnails-supernatant {
  position: absolute;
  bottom: 25px;
  width: 100%;
  color: #fff;
  background-color: rgba(51, 51, 51, 0.8);
  white-space: normal;
}
.wea-thumbnails-check {
  padding-bottom: 5px;
}



