.hide {
  display: none;
}

.wea-crm-list-wrapper li {
  width: 100%;
  position: relative;
  padding: 6px 0 6px 20px !important;
  border-bottom: 1px solid #e9e9e9;
  font-size: 12px;
  color: #333;
  overflow: hidden;
  cursor: pointer;
}

.wea-crm-list-wrapper li:hover {
  background-color: #eaf8fe;
}

.wea-crm-list-wrapper li.selected {
  background-color: #eaf8fe;
}

.wea-crm-list-wrapper li .icon-wrap {
  display: none;
  position: absolute;
  top: 0px;
  right: 0px;
  height: 0;
  width: 0;
  border-top: 21px solid #0b93da;
  border-left: 22px solid transparent;
}

.wea-crm-list-wrapper li .icon-coms-Selected {
  display: none;
  position: absolute;
  font-size: 11px;
  color: #fff;
  top: 0;
  right: 1px;
}

.wea-crm-list-wrapper li.selected .icon-wrap {
  display: block;
}

.wea-crm-list-wrapper li.selected .icon-coms-Selected {
  display: block;
}

.wea-crm-list .resource-content {
  display: inline-block;
  width: 100%;
  padding-left: 45px;
  min-height: 36px;
}

.wea-crm-list .item-wrap {
  position: relative;
}

.wea-crm-list .gray {
  color: #999;
  font-size: 12px;
}
.wea-crm-list .lastname {
  display: inline-block;
  min-width: 60px;
}
.wea-crm-list .breakall {
  word-break: break-all;
}
.wea-crm-list .overflow {
  -o-text-overflow: ellipsis;
     text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
}

.wea-crm-list .wea-tree-hr-portrait {
  width: 30px;
  height: 30px;
  border-radius: 15px;
  margin-right: 15px;
  vertical-align: initial;
  position: absolute;
  top: 5px;
}



