.wea-browser-single .scroll-wrapper {
    width: 100%;
    height: 100%;
}

.wea-browser-single .ant-spin-container {
    width: 100%;
}

.wea-browser-single .ant-modal-body {
    padding: 0px;
    min-height: 460px;
}

.wea-browser-single .ant-modal-content{
    overflow: hidden;
}
.wea-browser-single .ant-modal-content .ant-modal-header {
    padding: 0;
    background-color: #f9f9f9;
}
.wea-browser-single .ant-modal-content .ant-modal-header .ant-row {
    height: 48px;
}

.wea-browser-table-cursor .ant-table-tbody>tr {
    cursor: pointer;
}

.wea-browser-single-icon-circle {
    display: inline-block;
    vertical-align: middle;
    width: 24px;
    height: 24px;
    font-size: 16px;
    line-height: 24px;
    text-align: center;
    border-radius: 50%;
    margin-right: 10px;
    color: #fff;
}

.wea-browser-single .ant-table-tbody td a{
    color: #484848;
}
.wea-browser-single .ant-modal-footer button{
    border-radius: 3px;
}
.wea-browser-single .ant-modal-footer>button:first-child{
    margin-right: 10px;
}

/**/
.wea-browser-single .ant-table-pagination{
    margin: 0;
    float: none;
}

.wea-browser-single .wea-browser-table .ant-table-body{
    height: 360px;
    max-height: 360px!important;
}

.wea-browser-single.no-top .wea-browser-table .ant-table-body{
    height: 380px;
    max-height: 380px!important;
}

.wea-browser-single .wea-select-ctrl{
    padding: 0;
}

.wea-browser-single .ant-tree .ant-radio-wrapper {
  margin-right: 2px;
}

.wea-browser-single .wea-tree-hr-portrait {
  width: 30px;
  height: 30px;
  border-radius: 15px;
  vertical-align: middle;
}


