.wea-input-normal {
  font-size: 12px;
  color: #000;
  position: relative;
  display: inline-block;
  width: 100%;
  vertical-align: bottom;
  word-break: keep-all;
}
.wea-input-normal .text {
  display: inline-block;
  height: 100%;
}
.wea-input-normal .ant-input {
  height: inherit;
  min-height: 30px;
  font-size: inherit;
  padding: 0;
  padding-left: 5px;
  border-radius: 0px;
}
.wea-input-normal .input-tip {
  position: absolute;
  top: 6px;
  left: 100%;
}
.wea-input-normal .ant-input-wrapper {
  width: 100%;
  display: inline-block;
  height: inherit;
}
.wea-input-normal .password-strength-wrap {
  line-height: 1;
  padding-top: 6px;
}
.wea-input-normal .password-strength {
  display: inline-block;
  height: 8px;
  width: 86px;
  margin-right: 7px;
}
.wea-input-normal .isWeak {
  background: url("/cloudstore/resource/pc/com/images/weak.png") no-repeat 0 0;
}
.wea-input-normal .isMiddle {
  background: url("/cloudstore/resource/pc/com/images/middle.png") no-repeat 0 0;
}
.wea-input-normal .isStrong {
  background: url("/cloudstore/resource/pc/com/images/strong.png") no-repeat 0 0;
}



