.wea-date-switch .switch-picker {
  padding-left: 10px;
  padding-right: 10px;
  display: inline-block;
  -moz-user-select: none;
  /**/
  -webkit-user-select: none;
  /*webkit*/
  -ms-user-select: none;
  /*IE10*/
  /**/
  user-select: none;
}
.wea-date-switch .switch-picker .label {
  cursor: pointer;
  padding: 0 10px;
}
.wea-date-switch .switch-picker .icon-coms-Browse-box-delete,
.wea-date-switch .switch-picker .icon-coms-Browse-box-Add-to {
  position: relative;
  top: 3px;
  color: #bababa;
  font-size: 16px;
  cursor: pointer;
}



