.wea-transfer {
  position: relative;
  padding: 5px;
}
.wea-transfer .wea-transfer-left-wrapper {
  width: 50%;
  padding-right: 30px;
}
.wea-transfer .wea-transfer-right-wrapper {
  width: 50%;
  padding-left: 30px;
}
.wea-transfer .wea-transfer-opration {
  margin-top: -40px;
}
.wea-transfer .wea-transfer-list {
  position: relative;
  display: inline-block;
}
.wea-transfer .ant-tree li {
  margin: 0px;
}
.wea-transfer .tree-title {
  padding: 10px 5px;
  border-bottom: 1px solid #ddd;
}
.wea-transfer-list-wrapper {
  border: 1px solid #dadada;
}
.wea-transfer .wea-transfer-list .transfer-tree {
  padding: 0px;
}
.wea-transfer .wea-transfer-list .ant-tree-switcher {
  display: none;
}
.wea-transfer .wea-transfer-list .ant-tree-node-content-wrapper {
  padding: 0;
  display: block;
  position: relative;
}
.wea-transfer .wea-transfer-list .ant-tree-node-content-wrapper:hover {
  background-color: #eaf8fe;
}
.wea-transfer .wea-transfer-list .ant-tree-node-content-wrapper.ant-tree-node-selected {
  background-color: #eaf8fe;
}
.wea-transfer-list-wrapper li a.ant-tree-node-selected .wea-icon-selected-bg {
  display: block;
}
.wea-transfer-list-wrapper li a.ant-tree-node-selected .icon-coms-Selected {
  display: block;
}
.wea-transfer-list.borderRadius .wea-transfer-list-wrapper {
  border-radius: 5px;
}
.wea-transfer-list.borderRadius .wea-transfer-list-header {
  height: 40px;
  line-height: 40px;
  background-color: #F9FCFF;
  border-radius: 5px 5px 0 0;
  border-bottom: 1px solid #ddd;
}



